"use client";
import Image from "next/image";
import { motion } from "framer-motion";

export default function BusinessTaxSection() {
  return (
    <section className="container mx-auto px-4 py-12 space-y-16">
      {/* Section 1 */}
      <motion.div
        className="flex flex-col md:flex-row items-center gap-6"
        initial={{ opacity: 0, x: -50 }}
        whileInView={{ opacity: 1, x: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
      >
        <div className="md:w-1/2">
          <Image
            src="/pic1.png"
            alt="Analysis"
            width={600}
            height={340}
            className="rounded-lg hover:scale-105 transition-transform duration-500"
          />
        </div>
        <div className="md:w-1/2">
          <p className="text-[#1E1E1E] text-[16px]">
            Sole traders in Australia are required to lodge a tax return with
            the Australian Taxation Office (ATO) every year, even if their
            income is $0. Unlike companies, sole traders are taxed on their net
            business income, meaning they must report all earnings and deduct
            any business-related expenses to determine their taxable income...
          </p>
        </div>
      </motion.div>

      {/* Section 2 */}
      <motion.div
        className="flex flex-col md:flex-row-reverse items-center gap-6"
        initial={{ opacity: 0, x: 50 }}
        whileInView={{ opacity: 1, x: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
      >
        <div className="md:w-1/2">
          <Image
            src="/pic2.png"
            alt="Handshake"
            width={600}
            height={400}
            className="rounded-lg hover:scale-105 transition-transform duration-500"
          />
        </div>
        <div className="md:w-1/2">
          <p className="text-[#1E1E1E] text-[16px]">
            Partnerships are a widely used business structure in Australia,
            where two or more individuals operate a business together with the
            intention of making a profit...
          </p>
          <ul className="mt-4 space-y-4">
            {[
              "The partnership must file a tax return, but partners pay tax individually",
              "Each partner reports their share of income and deductions",
              "Timely and accurate lodgment helps avoid penalties",
            ].map((item, index) => (
              <li key={index} className="flex items-center gap-2 text-[#1E1E1E] group">
                <span className="bg-[#FF8B66] text-white w-6 h-6 flex items-center justify-center rounded-full group-hover:bg-orange-500 transition duration-300">
                  ✔️
                </span>
                {item}
              </li>
            ))}
          </ul>
        </div>
      </motion.div>

      {/* Section 3 */}
      <motion.div
        className="flex flex-col md:flex-row items-center gap-6"
        initial={{ opacity: 0, y: 50 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
      >
        <div className="md:w-1/2">
          <Image
            src="/pic3.png"
            alt="Meeting"
            width={600}
            height={400}
            className="rounded-lg hover:scale-105 transition-transform duration-500"
          />
        </div>
        <div className="md:w-1/2">
          <p className="text-[#1E1E1E] text-[16px]">
            Filing your company tax return online with the Australian Taxation
            Office (ATO) is a fast, convenient, and stress-free solution for
            businesses...
          </p>
        </div>
      </motion.div>

      {/* Section 4 */}
      <motion.div
        className="flex flex-col md:flex-row-reverse items-center gap-6"
        initial={{ opacity: 0, y: -50 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
      >
        <div className="md:w-1/2">
          <Image
            src="/pic4.png"
            alt="Teamwork"
            width={600}
            height={400}
            className="rounded-lg hover:scale-105 transition-transform duration-500"
          />
        </div>
        <div className="md:w-1/2">
          <p className="text-[#1E1E1E] text-[16px]">
            A trust tax return is essential for maintaining compliance and
            ensuring tax efficiency. Trusts are widely used for wealth
            management, asset protection, and tax planning...
          </p>
        </div>
      </motion.div>

      {/* CTA Button */}
      <motion.div
        className="mt-6 text-center"
        initial={{ opacity: 0, scale: 0.9 }}
        whileInView={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
      >
        <button className="bg-[#FE5722] text-white px-10 py-4 text-[18px] rounded-full font-semibold shadow-md hover:bg-orange-600 transition-transform duration-300 hover:scale-105">
          GET TAX RETURN
        </button>
      </motion.div>
    </section>
  );
}
